import { createStore } from "redux";
import rootReducer from "./reducer";
import { composeWithDevTools } from 'redux-devtools-extension'

const composeEnhancers = composeWithDevTools();

// createStore() nhn vo 3 tham s
// - tham s u tin l rootReducer l 1 function 
// dng  cp nht li cc state trong kho chung ny (kho store)
// - tham s th 2 l innitValue khi cn xt 1 gi tr mc nh no 
// - tham s th 3 l enhancers l 1 middle ware

const store = createStore(rootReducer, composeEnhancers);

export default store;
